import { Component, Input, OnInit } from '@angular/core';
import { Observable } from 'rxjs';
import { PermissionStore } from './@core/permissions/permission.store';
import { DataStoreService } from './@core/service/data-store.service';
import { RBACINFO } from './@core/urls/rbac-url.config';

@Component({
  selector: 'config-login-settings',
  template: `
    <lib-config-login-settings></lib-config-login-settings>
  `,
  styles: [
  ]
})
export class ConfigLoginSettingsComponent implements OnInit {
  @Input() public RBACORG?: RBACINFO = new RBACINFO();
  @Input() public PERMISSION?: any;
  @Input() public configureEvent!: Observable<any>;

  constructor(
    private permissionStore: PermissionStore,
    private _storeservice: DataStoreService
  ) {
  }
  ngOnInit() {
     this.configureEvent.subscribe((val: any) => {
      this.RBACORG = val.RBACORG;
      this.PERMISSION = val.PERMISSION;
      this._storeservice.setData('RBACORG', this.RBACORG);
      this.permissionStore.setStore(this.PERMISSION);
      this._storeservice.setData('HTTPSERVICE',val.HTTPSERVICE);
      this._storeservice.setData('AUTHSERVICE',val.AUTHSERVICE);
    })
  }

}
